<template>
  <div>

<nav-bar :name="title"></nav-bar>

    <v-container class="text-center">
      <br>
      <p> {{ data_store.name }} </p>
      <h5 class="text-uoverlinepercase text-center">
        <template>
          <v-chip
            :color="getColor(status)"
            dark
          >
            {{ status }}
          </v-chip>
        </template>
      </h5>
      <br>

    <v-card flat>
      <v-tabs
        v-model="tab"
        background-color="primary"
        dark
      >
        <v-tab
          v-for="item in items"
          :key="item.tab"
        >
          {{ item.tab }}
        </v-tab>
      </v-tabs>

      <v-tabs-items v-model="tab">
        <v-tab-item
          v-for="item in items"
          :key="item.tab"
        >
          <v-card flat>
            <v-card-text>{{ item.content }}</v-card-text>
          </v-card>
        </v-tab-item>
      </v-tabs-items>
    </v-card>
    
    </v-container>

    <footer-nav></footer-nav>

  </div>
</template>

<script>
import NavBar from '@/components/NavBar';
import FooterNav from '@/components/Footer';


  export default {
    components: {
      NavBar, FooterNav
    },

    data: () => ({
      title: 'STORE INFO',
        tab: null,
        items: [
          { tab: 'Basic Info', content: 'Tab 1 Content' },
          { tab: 'Owners', content: 'Tab 2 Content' },
          { tab: 'Invoices', content: 'Tab 3 Content' },
          { tab: 'Sales', content: 'Tab 4 Content' },
        ],
    }),

    computed:{
      /* SELECCIONA LA PRIMERA TIENDA DEL USUARIO */
        data_store(){
            return this.$store.state.auth.store_user;
        },
      /* CARGA TODAS LAS TIENDAS */
        stores(){
          return this.$store.state.auth.user.stores;
        },

        status(){
            switch(this.data_store.status){
                case 'A' : return 'ACTIVE';
                break;
                case 'S' : return 'SUSPENDED';
                break;
                case 'DA' : return 'DENIED ACTIVATION';
                break;
                case 'V' : return 'VACATION';
                break;
                case 'ED' : return 'EVERYDAY';
                break;
                default : return 'PENDING ACTIVATION';
                break;
            }
        }
    },

    methods: {
      getColor (status) {
        if (status == 'ACTIVE') return 'green'
        else if (status == 'EVERYDAY') return 'purple'
        else if (status == 'SUSPENDED') return 'red'
        else if (status == 'DENIED ACTIVATION') return 'gray'
        else if (status == 'VACATION') return 'yellow'
        else return 'orange'
      },
    }
  }
</script>